@import "./global/variables.scss";

.advantages__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.advantages__item {
  text-align: center;

  h3,
  p {
    margin: 0 auto;
    padding: 0;
  }
}

.advantages__item:nth-child(1) {
  margin-bottom: 31px;
}

.advantages__item:nth-child(2),
.advantages__item:nth-child(3),
.advantages__item:nth-child(4) {
  text-align: start;
}

.advantages__item:nth-child(1) .advantages__description {
  width: 282px;
  margin-left: 17px;
}

.advantages__item:nth-child(6),
.advantages__item:nth-child(7) {
  position: relative;

  &::before {
    position: absolute;
    content: "";
    width: 284px;
    height: 1px;
    background-color: #d6d6d6;
    left: 18px;
    bottom: 13px;
  }
}

.advantages__item__header {
  width: 320px;
  background-color: #81b3d2;
  color: #000000;
  padding-top: 49.5px;
  padding-bottom: 39px;

  p {
    margin-top: 11px;
  }
}

.advantages__item__header p:last-child {
  margin-top: 17px;
  margin-left: auto;
  margin-right: auto;
}

.advantages__title {
  font-family: "pt-sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  text-transform: uppercase;
}

.advantages__description {
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
}

.advantages-bridge__description {
  font-weight: 400;
}

.advantages__item__more {
  padding: 0;
  margin: 0;
  list-style-type: none;
  background-color: #ffffff;
  color: #000000;
  text-align: left;
}

.advantages__item__posts {
  background-color: #eeeeee;
  height: 150px;
  padding: 33px;
  padding-bottom: 6px;
  padding-top: 26px;

  .advantages__title {
    color: #000000;
    margin-bottom: 11px;
    margin-left: 0;
  }

  p {
    margin-bottom: 17px;
  }

  .advantages__description {
    font-family: "pt-sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: #333333;
  }
}

.advantages__image-city,
.advantages__image-bridge {
  display: block;
}

// mobile-only-version

@media (max-width: $mobile-width-only) {
  .advantages__item:first-child .advantages__item__header {
    position: relative;

    &::before {
      position: absolute;
      content: "";
      width: 100%;
      height: 17px;
      background-color: #ffffff;
      top: -1px;
      left: 0;
      clip-path: polygon(100% 0, 50% 100%, 0 0);
    }
  }

  .advantages__item {
    text-align: center;

    h3 {
      margin-left: -7px;
    }
  }

  .advantages__item:nth-child(5) {
    .advantages__item__header {
      padding: 38px 10px 35px 17px;
      box-sizing: border-box;

      p {
        padding-right: 8px;
      }
    }
  }

  .advantages__item:nth-child(7) {
    &::before {
      bottom: 12px;
    }

    .advantages__item__posts {
      padding-top: 10px;
    }
  }

  .advantages__item:nth-child(8) {
    .advantages__item__posts {
      margin-top: -32px;
      padding-top: 32px;

      .advantages__description:last-child {
        margin-bottom: 18px;
      }

      .advantages__title {
        margin-top: 12px;
      }
    }
  }
}

// tablet-version

@media (min-width: $tablet-width) {
  .advantages__list {
    display: grid;
    grid-template-columns: 256px 256px 256px;
    grid-template-rows: 256px auto 256px 256px;
  }

  .advantages__item:nth-child(1) {
    grid-column: 1/4;
    display: flex;
  }

  .advantages__item:nth-child(2) {
    grid-column: 1/2;
    grid-row: 2/3;
  }

  .advantages__item:nth-child(3) {
    grid-column: 2/3;
    grid-row: 2/3;
  }

  .advantages__item:nth-child(4) {
    grid-column: 3/4;
    grid-row: 2/3;
  }

  .advantages__item:nth-child(5) {
    grid-column: 1/4;
    grid-row: 3/4;
    display: flex;
  }

  .advantages__item:nth-child(2),
  .advantages__item:nth-child(3),
  .advantages__item:nth-child(4) {
    text-align: center;
  }

  .advantages__item:nth-child(6) div {
    padding: 41px 38px;
  }

  .advantages__item:nth-child(7) div,
  .advantages__item:nth-child(8) div {
    padding: 41px 49px;
  }

  .advantages__item:nth-child(6),
  .advantages__item:nth-child(7) {
    &::before {
      display: none;
    }
  }

  .advantages__item:nth-child(5) .advantages__title {
    width: 150px;
    margin: auto;
  }

  .advantages__item:nth-child(1) .advantages__description {
    width: 190px;
    margin-left: auto;
    margin-right: auto;
  }

  .advantages__item:nth-child(7) .advantages__description {
    width: 148px;
  }

  .advantages__item__header {
    width: 256px;
    height: 256px;
    padding: 43px 36px;
    box-sizing: border-box;
  }

  .advantages__image-city,
  .advantages__image-bridge {
    height: 256px;
    flex-grow: 1;
  }

  .advantages-bridge {
    order: 2;
  }

  .advantages__item__posts {
    width: 256px;
    height: 256px;
    box-sizing: border-box;
  }

  .advantages__title {
    font-size: 21px;
  }

  .advantages__title-first {
    width: 66%;
    margin: 0 auto;
  }

  .advantages__description-first {
    font-weight: 400;
  }
}

// desktop-version
@media (min-width: $desktop-width) {
  .advantages__list {
    display: grid;
    grid-template-columns: 400px 400px 400px;
    grid-template-rows: 256px auto 256px 256px;
  }

  .advantages__item:nth-child(1) .advantages__description {
    width: 290px;
  }

  .advantages__item:nth-child(7) .advantages__description {
    width: 277px;
    margin-left: auto;
    margin-right: auto;
  }


  .advantages__item__posts {
    width: 400px;
  }

  .advantages__item__header {
    width: 400px;
  }

  .advantages__item__posts .advantages__title {
    width: 150px;
    margin: 0 auto;
    margin-bottom: 21px;
  }

  .advantages__item__header-second {
    padding: 43px 92px;
  }

  .advantages__title-second {
    width: 150px;
  }
}
